
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import Uploader from 'vux-uploader'
import { mapState, mapActions, mapGetters } from "vuex";
export default {
components: {
Divider,
XButton,
Uploader
},
data () {
return {
post:this.$store.state.post
}
},
methods: {
createPost() {
this.$store.dispatch({
type: "updatePost"
}).then(res=>{this.$router.push({ name: 'post', params: { imageUrl: res }})});
}
},
mounted(){
this.$store.dispatch({
type: "getLocationAndWeather"
});
},
props: {
images: {
type: Array,
default: () => []
},
varmax: {
type: Number,
default: 9
},
showHeader: {
type: Boolean,
default: true
},
showTip: {
type: Boolean,
default: true
},
readonly: {
type: Boolean,
default: false
},
title: {
type: String,
default: '图片上传'
},

handleClick: {
type: Boolean,
default: true
},

autoUpload: {
type: Boolean,
default: false
},
uploadUrl: {
type: String,
default: "http://riqian.yingxiangempire.com/editor"
},
size: {
type: String,
default: 'normal'
},
capture: {
type: String,
default: ''
},
name: {
type: String,
default: 'img'
},
params: {
type: Object,
default: null
}
},
}



