import jQuery from "jquery"

export default {
  components: {},
  created() {
  },
  mounted(){
    return this.$nextTick(function () {
          this.doSomething() 
      })
  },
  methods: {
    doSomething() {
        $("#flipbook").turn({
          width: 400,
          height: 300,
          autoCenter: true
        });
    }
  }
};





